import React from 'react'
import featureimage from '../images/Frame 19.png'

function Features() {
   return (
      <div id='features'>
         <div className="features-model">
            <img src={featureimage} alt="" />
         </div>
         <div className="features-text">
            <h2>Features</h2>
            <h3>This Application<span> Software</span> is Art</h3>
            <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Nam ullam exercitationem optio doloribus, nobis veniam beatae cumque dolorem voluptatum inventore aut ipsa vitae id tempore? Cumque eligendi fugiat suscipit in!</p>
            <button>View More Features</button>
         </div>

      </div>
   )
}

export default Features

